<template>
    <div class="task-wrapper" :style="{ background: active?active.black_color:''}">
        <!--banner-->
        <div class="task-banner" v-if="active">
            <div class="banner-img">
                <img v-lazy="imgZip(active.banner,750)" alt=""/>
            </div>
            <span class="banner-reward-record" @click="eventGoRecord">奖励记录</span>
        </div>

        <div class="task-content" v-if="active && status">
            <!--领取奖励-->
            <section class="task-part part-one">
                <p class="one-title" v-if="status===3">快领取奖励开启成长任务吧</p>
                <p class="one-title" v-if="grow_done">恭喜你，你的成长值已满，任务完成</p>

                <div class="count-down" v-if="(status===2 || status===1) && !grow_done">
                    <span class="one-title count">距离任务结束还有</span>
                    <count-down :overTime="active.user_end_time" :isDay="true" :eventOver="overCountDown"/>
                </div>
                <div class="one-progress">
                    <div class="progress-line">
                        <span class="line-get" :style="{width: progressWidth}">
                            <span class="get-point" :style="progressPosStyle">当前成长值{{active.user_grow_rate}}</span>
                        </span>
                    </div>
                    <div class="progress-point">
                        <div class="prize-item" v-for="(item, i) in active.grow_reward" :key="i">
                            <span class="item-point">{{item.grow_rate}}</span>
                            <span class="item-img">
                                <img :src="item.image_url" alt=""/>
                            </span>
                            <span class="btn orange" v-if="item.status === 2"
                                  @click="eventClickReward(item, 1)">领取奖励</span>
                            <span class="btn gray" v-if="item.status === 3">已领取</span>
                        </div>
                    </div>
                </div>
            </section>

            <div class="task-title" v-if="day.length||cycle.length">
                <span class="title-text">完成以下任务即可获得成长值</span>
            </div>

            <!--每日任务-->
            <section class="task-part part-two" v-if="day.length">
                <div class="part-title">每日任务</div>
                <div class="part-title-small">每天更新，奖励当天内有效</div>
                <task-item v-for="(task, index) in day" :key="index" @onClickBtn="eventClickReward" :task="task"
                           :isGuide="isGuide" :isNewUser="isNewUser"/>
            </section>
            <!--探索任务-->
            <section class="task-part part-three" v-if="cycle.length">
                <div class="part-title">探索任务</div>
                <task-item v-for="(task, index) in cycle" :key="index" @onClickBtn="eventClickReward" :task="task"/>
            </section>

            <div class="foot-logo" v-if="status===2||status===3">
                <img :src="IMG_NEWTASK_LOGO_BOTTOM" alt=""/>
            </div>
        </div>

        <div class="task-no">
            <p class="no-hint" v-if="status===0">抱歉，活动仅限新用户参与</p>
            <p class="no-hint one" v-if="status===1">任务已经过期啦</p>
            <p class="no-hint two" v-if="status===1 && !grow_done">抱歉，你没有在规定时间获得足够的成长值</p>
        </div>

        <AwardModal v-if="isRewardModalShown" :modal-data="rewardModalData"
                    v-on:hideRewardModal="hideRewardModal"></AwardModal>

        <GuideModal v-if="isGuideModalShown" v-on:hideGuideModal="hideGuideModal"></GuideModal>

        <FullModal v-if="isFullModalShown" :modal-data="fullModalData" v-on:hideFullModal="hideFullModal"
                   v-on:getReward="requestTaskReward"></FullModal>
    </div>
</template>

<script>
    import {IMG_NEWTASK_LOGO_BOTTOM} from '@/const/images'
    import CountDown from '@/components/CountDown'
    import TaskItem from '@/components/newtask/TaskItem'
    import AwardModal from '@/components/newtask/AwardModal'
    import GuideModal from '@/components/newtask/GuideModal'
    import FullModal from '@/components/newtask/FullModal'
    import visibility from '@/mixins/visibility'
    import API from '@/server/api'
    import {local} from '@/utils/storage'
    import {platform} from '@/utils/platform'
    import {bridge} from '@/utils/native/bridge'
    import {imgZip} from '@/utils/tools'

    const ModalType = {
        'reward': 0,
        'full': 1,
        'guide': 2,
    };

    export default {
        name: 'NewTask',
        mixins: [visibility],
        components: {
            CountDown,
            TaskItem,
            AwardModal,
            GuideModal,
            FullModal,
        },
        data() {
            const that = this;
            return {
                status: 0,
                grow_done: 0,
                active: null,
                day: [],
                cycle: [],

                isGuide: false, // 是否显示过引导
                isRewardModalShown: false,
                rewardModalData: {},
                isGuideModalShown: false,
                isFullModalShown: false,
                fullModalData: {},
                modalTaskQueue: [],
                hasModal: false,

                progressWidth: 0,
                progressPosStyle: {},
                overCountDown() {
                    //倒计时完成
                    that.requestTaskInfo();
                },
                IMG_NEWTASK_LOGO_BOTTOM,
                imgZip
            }
        },
        computed: {
            firstUnfinishedGrowthTask() {
                if (this.active && this.active.grow_reward) {
                    return this.getFirstUnfinishedGrowthTask(this.active.grow_reward)
                } else {
                    return {}
                }
            },
            isNewUser() {
                return this.status === 3
            },
            activity_id() {
                if (this.active) {
                    return this.active.novice_id
                } else {
                    return ''
                }
            },
            user_grow_rate() {
                if (this.active) {
                    return this.active.user_grow_rate
                } else {
                    return ''
                }
            }
        },
        watch: {
            firstUnfinishedGrowthTask(newTask, oldTask) {
                // 判断成长值满
                if (oldTask.task_id && oldTask.task_id !== newTask.task_id) {
                    this.createModalTask({
                        type: ModalType.full,
                        payload: Object.assign({
                            activity_id: this.activity_id,
                            user_grow_rate: this.user_grow_rate
                        }, oldTask)
                    });
                }
            }
        },
        created() {
            let that = this;
            this.requestTaskInfo({},(data)=>{
                const {status, active} = data;
                let value = '';
                if(status === 0) {value='不符合条件'}
                if(status === 1) {value='已结束'}
                if(status === 2) {value='进行中'}
                if(status === 3) {value='未开启'}
                that.log({
                    cid: 'newer_task',
                    aid: 'index',
                    value,
                    activity_id: active.novice_id
                });
            });
        },
        mounted() {
            this.setApp();
        },
        methods: {
            //获取页面信息
            async requestTaskInfo(loading = true, callback=null) {
                const {data} = await this.$http.get(API.newTask('attend'), {}, loading);
                if (data) {
                    this.updateData(data);
                    this.progressLine();
                    this.loadLocalGuide();
                    callback && callback(data);
                }
            },
            //领取 任务奖励&&满积分奖励
            async requestTaskReward(task_id, grow) {
                const {data} = await this.$http.get(API.newTask(grow ? 'growReward' : 'taskReward'), {task_id}, false);
                if (data) {
                    let {reward} = data;
                    if (reward && reward.type) {
                        reward.type = reward.type*1;
                    }

                    let payload = Object.assign({
                        isNewUser: this.isNewUser,
                        activity_id: this.activity_id,
                    }, data);

                    if (grow) {
                        let names = [];
                        if (data.grow_rate) {
                            names.push('成长值')
                        }

                        if (data.reward && reward.type) {
                            switch (reward.type) {
                                case 1:
                                    names.push('积分');
                                    break;
                                case 2:
                                    names.push('优惠券');
                                    break;
                                case 3:
                                    names.push('优惠券包');
                                    break;
                            }
                        }

                        this.log({
                            cid: 'newer_task',
                            aid: 'get_full_reward_success',
                            value: names.join('|'),
                            activity_id: this.active.novice_id,
                            index: payload.grow_rate,
                        });

                        // 成长任务没有成长值奖励
                        payload.grow_rate = '';
                    }

                    this.createModalTask({
                        type: ModalType.reward,
                        payload: payload
                    });

                    // 没引导过的新用户，弹出引导
                    if (!grow && this.isNewUser && !this.isGuide) {
                        this.createModalTask({
                            type: ModalType.guide
                        })
                    }

                    //更新app首页
                    if(platform.isApp){
                        bridge.refreshNovice();
                    }

                    this.requestTaskInfo(false);
                }
            },

            progressLine() {
                let {user_grow_rate, grow_reward} = this.active;
                let points = grow_reward.map(grow => grow.grow_rate);
                let growsLength = grow_reward.length;
                let pointVal = 0;       //总进度
                let lastPoint = 100;    //最后一段值

                //计算百分比
                points.unshift(0);
                points.push(points.max() + lastPoint);
                user_grow_rate = Math.min(user_grow_rate, points.max());
                let pointsIndex = points.arrayIndex(user_grow_rate);
                points.forEach((o, i) => {
                    if (i <= pointsIndex) {
                        let pointNow = points[i + 1] - points[i];
                        let pointNowVal = Math.min(user_grow_rate - points[i], pointNow);
                        let percent = 12;   //头尾百分比
                        if (i > 0 && i < points.length - 2) {
                            percent = (100 - percent * 2) / (growsLength - 1);
                        }
                        pointVal += pointNowVal / pointNow * percent;
                    }
                });
                this.progressWidth = pointVal.toFixed(2) + '%';

                //调整两头成长值跟随位置
                if (user_grow_rate < points[1]) {
                    this.progressPosStyle = {left: '-5px'};
                }else if (user_grow_rate > points[points.length - 2]) {
                    let diff = user_grow_rate - points.max();
                    this.progressPosStyle = {right: (diff / 2.5 - 6) + 'px'};
                }else{
                    this.progressPosStyle = {right: '-47px'};
                }
            },
            updateData(data) {
                let {status, grow_done, active, day, cycle} = data;
                this.status = status;
                this.grow_done = grow_done;
                this.active = active;
                this.day = day;
                this.cycle = cycle;
            },
            loadLocalGuide() {
                let {is_guide} = local.load('NEW_TASK');
                this.isGuide = is_guide;
            },
            eventClickReward(item, grow) {
                const {status, task_id, grow_rate} = item;
                if(grow){
                    this.log({
                        cid: 'newer_task',
                        aid: 'get_full_reward_bar',
                        activity_id: this.active.novice_id,
                        index: grow_rate
                    });
                }
                if (status === 2) {
                    this.requestTaskReward(task_id, grow);
                }
            },
            eventGoRecord() {
                this.log({
                    cid: 'newer_task',
                    aid: 'newer_task_record',
                    activity_id: this.active.novice_id
                });
                this.openUrl({path: '/newTask/record'});
            },
            showRewardModal(data) {
                this.rewardModalData = data;
                this.isRewardModalShown = true;

                this.log({
                    cid: 'newer_task',
                    aid: 'get_grow_exposure',
                    value: data.grow_rate,
                    activity_id: this.active.novice_id,
                    name: data.name
                });
            },
            hideRewardModal() {
                this.isRewardModalShown = false;
                this.hasModal = false;
                this.showNextModal();
            },
            showGuideModal() {
                this.isGuideModalShown = true;
                local.update('NEW_TASK', {is_guide: 1});
                this.isGuide = true;

                this.log({
                    cid: 'newer_task',
                    aid: 'rules_instruction_exposure',
                    activity_id: this.active.novice_id
                });
            },
            hideGuideModal() {
                this.isGuideModalShown = false;
                this.hasModal = false;
                this.showNextModal();
            },
            showFullModal(data) {
                this.fullModalData = data;
                this.isFullModalShown = true;

                this.log({
                    cid: 'newer_task',
                    aid: 'get_full_reward_exposure',
                    activity_id: this.active.novice_id,
                    index: data.grow_rate
                });
            },
            hideFullModal() {
                this.isFullModalShown = false;
                this.hasModal = false;
                this.showNextModal();
            },
            showNextModal() {
                if (this.hasModal || !this.modalTaskQueue.length) return;
                let task = this.modalTaskQueue.shift();
                switch (task.type) {
                    case ModalType.reward:
                        this.showRewardModal(task.payload);
                        break;
                    case ModalType.full:
                        this.showFullModal(task.payload);
                        break;
                    case ModalType.guide:
                        this.showGuideModal();
                        break;
                }

                this.hasModal = true;
            },
            createModalTask(data) {
                this.modalTaskQueue.push(data);
                this.showNextModal();
            },
            getFirstUnfinishedGrowthTask(arr) {
                let found = false;
                let result = {};
                arr.forEach((task) => {
                    if (!found && task.status === 1) {
                        found = true;
                        result = task;
                    }
                });
                return result;
            },
            setApp() {
                if (!platform.isApp) return;
                this.eventListenerApp('APP_E3', () => {
                    this.log({
                        cid: 'newer_task',
                        aid: 'rules',
                        activity_id: this.active.novice_id
                    });
                    this.openUrl({path: 'https://maicai.api.ddxq.mobi/info/single',query:{alias:'newer_task_point'}});
                });
                this.visibilityAppShow(() => {
                    this.requestTaskInfo(false);
                });
                bridge.setRightTopButton({title: '规则'});
            }
        }
    }
</script>

