<template>
    <div class="count-down">
        <div class="time-part" :style="dayStyle" v-if="showDays">{{days}}天</div>
        <div class="time-part timing" v-if="!showDays">
            <span class="time" :style="timeStyle">{{times[0]}}</span>
            <span class="time-concat" :style="timeConcat">:</span>
            <span class="time" :style="timeStyle">{{times[1]}}</span>
            <span class="time-concat" :style="timeConcat">:</span>
            <span class="time" :style="timeStyle">{{times[2]}}</span>
        </div>
    </div>
</template>

<script>
    let timeOut = null;
    export default {
        props: {
            //是否先显示天数,不满24小时自动切换倒计时
            isDay: {
                default: false
            },
            overTime: Number,
            timeSize: {
                default: '0.29'
            },
            fontColor: {
                default: '#ffffff'
            },
            bgColor: {
                default: '#FA5C47'
            },
            daySize: {
                default: '0.4'
            },
            dayColor: {
                default: '#FA5C47'
            },
            //倒计时完成回调
            eventOver: {}
        },
        mounted() {
            this.differTime = this.$store.state.activity.differ_time;
            if (!this.isDay) {
                this._checkTime();
            } else {
                this._checkDay();
            }
        },
        data() {
            return {
                times: [],
                days: 0,
                showDays: this.isDay,
                timeStyle: {
                    background: this.bgColor,
                    color: this.fontColor,
                    fontSize: this.timeSize + 'rem'
                },
                timeConcat: {
                    color: this.bgColor,
                    fontSize: this.timeSize + 'rem'
                },
                dayStyle: {
                    color: this.dayColor,
                    fontSize: this.daySize + 'rem'
                },
                differTime: 0
            }
        },
        methods: {
            _timeStr(t) {
                let h = parseInt(t / 3600);
                let m = parseInt(t % 86400 % 3600 / 60);
                let s = parseInt(t % 86400 % 3600 % 60);
                if (h <= 9) h = '0' + h;
                if (m <= 9) m = '0' + m;
                if (s <= 9) s = '0' + s;
                return [h, m, s];
            },
            _checkTime() {
                let nowTime = parseInt(new Date().getTime() / 1000);
                let overTime = this.overTime;
                let differTime = this.differTime;
                if (overTime >= (nowTime + differTime)) {
                    this.times = this._timeStr(overTime - (nowTime + differTime));
                    timeOut = setTimeout(() => {
                        this._checkTime();
                    }, 1000);
                } else {
                    //倒计时完成
                    this.times = ['00', '00', '00'];
                    this.eventOver();
                    timeOut && clearTimeout(timeOut)
                }
            },
            _checkDay() {
                let nowTime = parseInt(new Date().getTime() / 1000);
                let overTime = this.overTime;
                let differTime = this.differTime;
                if (overTime >= (nowTime + differTime)) {
                    this.days = parseInt((overTime - (nowTime + differTime)) / 3600 / 24);
                    if (!this.days) {
                        this.showDays = false;
                        this._checkTime();
                    }
                } else {
                    this.eventOver();
                }
            }
        },
        destroyed() {
            timeOut && clearTimeout(timeOut)
        }
    }
</script>

<style lang="scss" scoped>
    .count-down {
        display: inline-block;
        font-weight: bolder;
        .time-part {
            @include ibm;
            &.timing {
                min-width: 140px;
                text-align: left;
            }
        }
        .time {
            @include ibm;
            border-radius: 3px;
            padding: 2PX 3PX 1PX 3PX;
        }
        .time-concat {
            margin: 0 4px;
            @include ibm
        }
    }
</style>