<template>
    <div class="task-item">
        <div class="task-item-icon">
            <img :src="ENUM[task.subtype]['icon']" alt=""/>
        </div>
        <div class="task-item-info">
            <div class="info-detail">
                <p class="info-name">
                    <span class="info-name-text">{{task.name}}</span>
                    <span class="info-name-day" v-if="task.task_days">限时{{task.task_days}}天</span></p>
                <p class="info-desc">{{task.desc}}</p>
                <div class="info-prize" v-if="task.subtype === 13 || task.subtype === 12">
                    <div class="prize-integral" v-if="task.reward_type === 1">
                        <div class="prize-integral-num">{{task.point_value}}</div>
                        <div class="prize-integral-text">
                            <p>抵现</p>
                            <p>积分</p>
                        </div>
                    </div>
                    <div class="prize-coupon" v-if="task.reward_type === 2">
                        <div class="prize-coupon-price">
                            <span class="money-mark">¥</span>
                            <span class="money-number" :style="couponMoneyStyle">{{task.coupon_money}}</span>
                        </div>
                        <div class="prize-coupon-info">
                            <p class="coupon-info-desc">{{task.coupon_name}}</p>
                            <p class="coupon-info-desc">{{task.coupon_desc}}</p>
                        </div>
                    </div>
                    <div class="prize-coupon-pack" v-if="task.reward_type === 3">
                        <div class="prize-coupon-price">
                            <span class="money-mark">¥</span>
                            <span class="money-number" :style="couponMoneyStyle">{{task.coupon_money}}</span>
                        </div>
                        <div class="prize-coupon-info">
                            <p class="coupon-info-desc">{{task.coupon_name}}</p>
                            <p class="coupon-info-desc"><span class="coupon-pack-tag">组合券礼包</span></p>
                        </div>
                    </div>
                    <div class="prize-product" v-if="task.reward_type === 4">
                        <div class="product-img">
                            <img :src="task.product_img" alt=""/>
                        </div>
                        <div class="product-info">
                            <p class="product-name">{{task.product_name}}</p>
                            <div class="product-price">
                                <span class="price-number">原价¥{{task.product_price}}</span>
                                <span class="price-tag">免费</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="info-btn">
                <span class="btn orange" @click="_eventReward" v-if="task.status === 2">领取奖励</span>
                <span class="btn red" @click="_eventReward"
                      v-if="task.status === 1">{{ENUM[task.subtype]['btnText']}}</span>
                <span class="btn gray" v-if="task.status === 3">已领取</span>
                <span class="btn gray" v-if="task.status === 4">明天再来</span>
                <span class="guide-finger" v-if="isNewUser && !isGuide && task.subtype === 11"
                      @click="_eventReward"></span>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        IMG_NEWTASK_ICON_BUY,
        IMG_NEWTASK_ICON_DAY,
        IMG_NEWTASK_ICON_EVALUATE,
        IMG_NEWTASK_ICON_FISH,
        IMG_NEWTASK_ICON_INFO,
        IMG_NEWTASK_ICON_INVITE,
        IMG_NEWTASK_ICON_MEAT,
        IMG_NEWTASK_ICON_SIGN,
        IMG_NEWTASK_ICON_VIP
    } from '@/const/images'
    import {bridge} from '@/utils/native/bridge'

    export default {
        name: "TaskItem",
        props: {
            task: {},
            isGuide: {
                default: false
            },
            isNewUser: {
                default: true
            }
        },
        data() {
            let that = this;
            return {
                ENUM: {
                    11: {
                        icon: IMG_NEWTASK_ICON_DAY,
                        btnText: '去登录',
                        done() {
                        }
                    },
                    12: {
                        icon: IMG_NEWTASK_ICON_BUY,
                        btnText: '去购物',
                        done() {
                            that.openUrl({path: 'ddxq://product/category'})
                        }
                    },
                    13: {
                        icon: IMG_NEWTASK_ICON_BUY,
                        btnText: '去购物',
                        done() {
                            that.openUrl({path: 'ddxq://product/category'})
                        }
                    },
                    21: {
                        icon: IMG_NEWTASK_ICON_SIGN,
                        btnText: '去签到',
                        done() {
                            that.openUrl({path: 'maicai:/integral'})
                        }
                    },

                    23: {
                        icon: IMG_NEWTASK_ICON_INVITE,
                        btnText: '去邀请',
                        done() {
                            that.openUrl({path: 'maicai:/gift/earn'})
                        }
                    },
                    24: {
                        icon: IMG_NEWTASK_ICON_VIP,
                        btnText: '去开通',
                        done() {
                            that.openUrl({path: '/vip'})
                        }
                    },
                    25: {
                        icon: IMG_NEWTASK_ICON_EVALUATE,
                        btnText: '去评价',
                        done() {
                            that.openUrl({path: 'ddxq://order/list', query: {type: 0, state: 3}});
                        }
                    },
                    26: {
                        icon: IMG_NEWTASK_ICON_INFO,
                        btnText: '去完成',
                        done() {
                            bridge.gotoUserInfo();
                        }
                    },
                    27: {
                        icon: IMG_NEWTASK_ICON_FISH,
                        btnText: '去购物',
                        done() {
                            that.openUrl({
                                path: 'ddxq://product/category',
                                query: {firstId: '58fefb94936edf5f5d8b4767'}
                            })
                        }
                    },
                    28: {
                        icon: IMG_NEWTASK_ICON_MEAT,
                        btnText: '去购物',
                        done() {
                            that.openUrl({
                                path: 'ddxq://product/category',
                                query: {firstId: '58fefa38916edf9b128b45fc'}
                            })
                        }
                    }

                },
                couponMoneyStyle: {}
            }
        },
        mounted() {
            this._couponStyle();
        },
        watch:{
            task(newTask){
                const that = this;
                const {reward_type} = newTask;
                if(reward_type){
                    that._couponStyle();
                }
            }
        },
        methods: {
            _couponStyle() {
                let typeAndLength = this.task.reward_type;
                let couponMoney = this.task.coupon_money;
                if (!typeAndLength || !couponMoney) return;
                couponMoney = JSON.stringify(couponMoney);
                if (this.task.reward_type === typeAndLength) {
                    if (couponMoney.length > typeAndLength + 1) {
                        this.couponMoneyStyle = {
                            fontSize: '20px'
                        }
                    }
                }
            },
            _eventReward() {
                const task = this.task;
                const {subtype, name, novice_id, status} = task;

                this.log({
                    cid: 'newer_task',
                    aid: 'click_button',
                    value: status === 2?'领取奖励':this.ENUM[subtype]['btnText'],
                    activity_id: novice_id,
                    name
                });

                if (task.status === 2) {
                    this.$emit("onClickBtn", task);
                }
                if (task.status === 1) {
                    this.ENUM[subtype]['done']();
                }
            }
        }
    }
</script>

