<template>
    <div>
        <div class="mask" @click="hideReward"></div>
        <div class="modal-main">
            <div class="modal-content">
                <h2>领取成功</h2>
                <div class="modal-prize">
                    <div class="growth-ctr" v-if="type === 1">
                        <div class="growth">
                            <div class="circle">+{{grow_rate}}</div>
                            <div class="text">成长值*{{grow_rate}}</div>
                        </div>
                    </div>
                    <div class="growth-point" v-if="type === 2">
                        <div class="growth" v-if="grow_rate">
                            <div class="circle">+{{grow_rate}}</div>
                            <div class="text">成长值*{{grow_rate}}</div>
                        </div>
                        <div class="point">
                            <div class="bg">
                                <div class="num">{{reward.point_value}}</div>
                                <div class="text-ctr">
                                    <div class="text1">抵现</div>
                                    <div class="text2">积分</div>
                                </div>
                            </div>
                            <div class="text">积分{{reward.point_value}}</div>
                        </div>
                    </div>
                    <div class="coupon-ctr" v-if="type === 3">
                        <div class="growth1" v-if="grow_rate">
                            <div class="circle1">+{{grow_rate}}</div>
                            <div class="text1">成长值*{{grow_rate}}</div>
                        </div>
                        <div class="coupon-wrap">
                            <div class="coupon-scroll">
                                <div class="coupon" v-bind:key="i" v-for="(item, i) in couponList" @click="useCoupon(item)">
                                    <div class="coupon-num">
                                        <div class="r1">
                                            <span class="unit">¥</span>
                                            <span class="num">{{item.money}}</span>
                                        </div>
                                        <div class="r2">满{{item.pay_min}}元使用</div>
                                    </div>
                                    <div class="coupon-detail">
                                        <div class="r1">{{item.name}}</div>
                                        <div class="r2">{{item.description}}</div>
                                        <div class="r3">{{item.valid_days}}天后过期</div>
                                    </div>
                                    <div class="coupon-use">去使用</div>
                                </div>
                            </div>
                            <div class="coupon-mask" v-show="couponList.length > 2"></div>
                        </div>
                    </div>
                </div>
                <div class="modal-tip" v-if="cycle_days">
                    您已开启新人成长任务， 有效期为{{cycle_days}}天
                </div>
                <div class="modal-tip" v-if="type === 2">
                    积分可至我的积分页面查询
                </div>
                <div class="modal-footer">
                    <div class="confirm" @click="confirm">确定</div>
                </div>
            </div>
            <icon-font class="modal-close" name="gouwuchedankuangguanbi" color="#ffffff" size="44" @click="hideReward"/>
        </div>
    </div>
</template>

<script>
    import {platform} from '@/utils/platform'
    import {APP_DOWN} from '@/const/urls'

    export default {
        name: "AwardModal",
        props: {
            modalData: {
                default: () => {},
                type: Object
            }
        },
        methods: {
            confirm() {
                this.hideReward();
                this.log({
                    cid: 'newer_task',
                    aid: 'get_grow_confirm',
                    activity_id: this.modalData.activity_id,
                    name: this.modalData.name
                });
            },
            hideReward() {
                this.$emit('hideRewardModal')
            },
            isCoupon(type) {
                return type === 2 || type === 3
            },
            useCoupon(item) {
                if (item.url) {
                    this.openUrl({path: item.url})
                } else {
                    this.openUrl({path: platform.isApp ? 'ddxq://product/home' : APP_DOWN});
                }
            }
        },
        computed: {
            type() {
                let reward = this.reward;

                if (reward) {
                    if (reward.type === 1) {
                        return 2;
                    } else if (this.isCoupon(reward.type)) {
                        return 3;
                    }
                }

                return 1;
            },
            grow_rate() {
                return this.modalData.grow_rate
            },
            isNewUser() {
                return this.modalData.isNewUser
            },
            reward() {
                return this.modalData.reward
            },
            cycle_days() {
                return this.modalData.cycle_days
            },
            couponList() {
                let reward = this.reward;
                let result = [];
                if (reward) {
                    if (reward.type === 2) {
                        result = reward.coupon;
                    } else if (reward.type === 3) {
                        result = reward.coupon_package;
                    }
                }
                return result || [];
            }
        }
    }
</script>

